import React from 'react';
import styled from 'styled-components';
import Error from '../Components/Common/Error';
import { useLocation } from 'react-router-dom';

const ErrorCheck = ({ errorCheck }) => {
  // ,    ShowDetailPage  
  if (errorCheck === 'invalidAccess') {
    return <Error text="   :(" buttonStyle="invalidAccess"></Error>;
  }
  // errorCheck  '/'       :  
  else if (errorCheck && errorCheck.startsWith('/')) {
    return <Error text="   :(" buttonStyle="tokenError"></Error>;
  }
  //        
  else {
    return <Error text="    :(" buttonStyle="404"></Error>;
  }
};

const ErrorPage = () => {
  // error      
  const errorCheck = useLocation().state;
  console.log(errorCheck);

  return (
    <SContainer>
      <ErrorCheck errorCheck={errorCheck} />
    </SContainer>
  );
};

export default ErrorPage;

const SContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
`;
