import React from 'react';
import styled from 'styled-components';
import Button from './Button';
import TopBarBtn from './TopBarBtn';
import SearchInput from '../Search/SearchInput';
import iconLogo from '../../Assets/Icon/logo-text.svg';
import iconArrow from '../../Assets/Icon/icon-arrow-left.svg';
import iconLogout from '../../Assets/Icon/icon-logout.svg';
import iconMore from '../../Assets/Icon/icon-more-vertical.svg';
import { useNavigate } from 'react-router-dom';

//   :     
const LeftEl = ({ leftEl, setKeyword }) => {
  //  
  const navigate = useNavigate();
  const handleClick = () => {
    navigate(-1);
  };
  if (leftEl === 'back') {
    return <TopBarBtn icon={iconArrow} altTxt={''} onClick={handleClick} />;
  } else if (leftEl === 'search') {
    return <SearchInput setKeyword={setKeyword} />;
  } else {
    return <TopBarBtn cursor="unset" id="showLogo" icon={iconLogo} altTxt={'! '} />;
  }
};

//   :   null
const RightEl = ({ rightEl }) => {
  if (rightEl === 'newPost') {
    return (
      <Button color="white" active="active">
        
      </Button>
    );
  } else if (rightEl === 'save') {
    return <Button disabled></Button>;
  } else if (rightEl === 'upload') {
    return <Button size="Small"></Button>;
  } else if (rightEl === 'logout') {
    return <TopBarBtn icon={iconLogout} altTxt={''} />;
  } else if (rightEl === 'more') {
    return <TopBarBtn icon={iconMore} altTxt={''} />;
  } else {
    return null;
  }
};

//   
const TopBar = ({ leftEl, rightEl, setKeyword }) => {
  return (
    <>
      <SHeader>
        <LeftEl leftEl={leftEl} setKeyword={setKeyword} />
        <RightEl rightEl={rightEl} />
      </SHeader>
    </>
  );
};

export default TopBar;

const SHeader = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 390px;
  height: 48px;
  padding: 0 16px;
  border-bottom: solid 1px var(--gray);
  box-sizing: border-box;
  position: fixed;
  top: 0;
  background-color: #fff;
  #showLogo {
    padding-top: 3px;
    width: 115px;
    height: 28px;
  }
  form {
    width: 100%;
    /* padding: 0 0 0 15px; */
    input {
      width: calc(100% - 30px);
      height: 32px;
      background-color: #f2f2f2;
      border-radius: 30px;
      padding: 0 15px;
      &::placeholder {
        color: #c4c4c4;
      }
    }
  }
`;
