import React, { useState } from 'react';
import styled from 'styled-components';
import { useLocation, useNavigate } from 'react-router-dom';

//  
import Profile from '../Components/Common/Profile';
import PostLayoutButtons from '../Components/Common/Post/PostLayoutButtons';
import Post from '../Components/Common/Post/Post';
import BottomNav from '../Components/Common/BottomNav';
import AlertModal from '../Components/Modal/Alert';
import TopBtn from '../Components/Common/TopBtn';

// recoil
import { MyAccountName } from '../Atom/atom';
import { useRecoilValue } from 'recoil';

// API
import { GetUserPostAPI } from '../API/PostAPI';

// assets
import logoutSVG from '../Assets/Icon/icon-logout.svg';
import arrowSVG from '../Assets/Icon/icon-arrow-left.svg';

// hooks
import useScrollToTop from '../Hook/useScrollToTop';
import useAtomReset from '../Hook/useAtomReset';

const ProfileDetailPage = () => {
  //  ,     
  const getMyAccountName = useRecoilValue(MyAccountName);
  // console.log(getMyAccountName);

  //    ,     
  //       let 
  let otherAccountName = useLocation().state;

  //       
  //     BottomNav.jsx  accountname 
  //      
  if (otherAccountName === { GetMyAccountName: getMyAccountName }) {
    otherAccountName = otherAccountName.GetMyAccountName;
  }

  const postsData = GetUserPostAPI(otherAccountName);

  //  
  const navigate = useNavigate();
  const arrow = () => {
    navigate(-1);
  };

  //     
  const [isLogout, setIsLogout] = useState(false);
  const closeModal = () => {
    setIsLogout(false);
  };
  const openModal = () => [setIsLogout(true)];

  // scroll to top
  const scrollController = useScrollToTop();

  //  recoil atom   Hook
  const showInSeoulLogout = useAtomReset();

  return (
    // getMyAcoountName accountname  ,
    //        
    //  ,      
    <>
      {getMyAccountName === otherAccountName ? (
        <STopBar>
          <button onClick={arrow} className="arrowBtn">
            <img src={arrowSVG} alt="" />
          </button>
          <button onClick={openModal} className="logoutBtn">
            <img src={logoutSVG} alt="" />
          </button>
        </STopBar>
      ) : (
        <STopBar>
          <button onClick={arrow} className="arrowBtn">
            <img src={arrowSVG} alt="" />
          </button>
        </STopBar>
      )}

      <SProfileWrapper ref={scrollController.sectionLayoutRef} onScroll={scrollController.handleScroll}>
        <Profile accountname={otherAccountName} />
        <PostLayoutButtons />
        {postsData?.length > 0 ? (
          postsData.map(postsData => <Post postsData={postsData} />)
        ) : (
          <li style={{ display: 'none' }} className="noContent">
              .
          </li>
        )}
      </SProfileWrapper>
      {isLogout && (
        <AlertModal confirmText="" onConfirm={showInSeoulLogout} onCancel={closeModal}>
           ?
        </AlertModal>
      )}
      <TopBtn scrollPosition={scrollController.scrollPosition} sectionLayoutRef={scrollController.sectionLayoutRef} />
      <BottomNav />
    </>
  );
};

export default ProfileDetailPage;

const STopBar = styled.div`
  width: 390px;
  height: 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  border-bottom: solid 1px var(--gray);
  position: fixed;
  top: 0;
  background-color: #fff;
  button {
    width: 22px;
    height: 22px;
    img {
      width: 100%;
      height: 100%;
    }
  }
`;

const SProfileWrapper = styled.div`
  height: calc(100vh - 77px);
  overflow-y: scroll;
  padding-bottom: 20px;
  &::-webkit-scrollbar {
    display: none;
  }
`;
