import React from 'react';
import styled from 'styled-components';
import PostHeader from './PostHeader';
import PostContentButtons from './PostContentButtons';
import PostContent from './PostContent';
import { Link } from 'react-router-dom';

// postsData PostPage       .
// profileData  postsData    .
const Post = ({ postsData }) => {
  const postYearMonth = postsData.createdAt?.split('-');
  const postYear = postYearMonth[0];
  const postMonth = postYearMonth[1];
  const postDay = postYearMonth[2].split('T')[0];

  return (
    <>
      {/*     */}
      <SPostSection>
        {/*   */}
        <PostHeader postsData={postsData} />
        {/*    Div */}
        <SPostContentDiv>
          <Link to="/postdetailpage" state={postsData}>
            {/*  ( ) */}
            <PostContent postsData={postsData} />
          </Link>
          {/*  ,    */}
          <PostContentButtons postsData={postsData} />
          {/*   ,   */}
          <SPostContentDate>
            {postYear} {postMonth} {postDay}
          </SPostContentDate>
        </SPostContentDiv>
      </SPostSection>
    </>
  );
};

export default Post;

//  
const SPostSection = styled.section`
  width: 358px;
  margin: 16px 16px 30px;
  button {
    background-color: #ffffff;
  }
`;

//   
const SPostContentDiv = styled.div`
  /* margin-left: 54px; */
`;

//   ,  
const SPostContentDate = styled.p`
  font-size: 10px;
  font-weight: 400;
  color: #767676;
  margin-top: 12px;
  cursor: pointer;
`;
