import React, { useState } from 'react';
import styled from 'styled-components';
import TopBarBtn from '../Common/TopBarBtn';
import iconSearch from '../../Assets/Icon/icon-search.svg';

const SearchInput = ({ setKeyword }) => {
  const [inputValue, setInputValue] = useState('');

  //  
  const submitButtonHandler = e => {
    e.preventDefault();
    setKeyword(inputValue);
  };

  //  
  const submitKeyDownHandler = e => {
    if (e.key === 'Enter') {
      submitButtonHandler(e);
    }
  };

  // value 
  const valueChangeHandler = e => {
    setInputValue(e.target.value);
  };

  return (
    <SSearchInput>
      {/* onChange   inputValue  */}
      <input
        type="text"
        placeholder=" "
        onKeyDown={submitKeyDownHandler}
        onChange={valueChangeHandler}
        value={inputValue}
      />
      <TopBarBtn type="submit" onClick={submitButtonHandler} icon={iconSearch} alt={''} />
    </SSearchInput>
  );
};

export default SearchInput;

const SSearchInput = styled.form`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 390px;
  input {
    width: calc(100% - 30px);
    height: 32px;
    background-color: #f2f2f2;
    border-radius: 30px;
    padding: 0 15px;
    margin-right: 15px;
    &::placeholder {
      color: #c4c4c4;
    }
  }
`;
