import React, { useState } from 'react';
import styled from 'styled-components';
import SInputBox from '../Common/InputBox';
import Button from '../Common/Button';
import { IsValidEmailAPI, LoginAPI } from '../../API/User';
import { useNavigate } from 'react-router-dom';
import { Token, MyAccountName, IsLoginState } from '../../Atom/atom';
import { useSetRecoilState } from 'recoil';

function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [FailedMessage, setFailedMessage] = useState('');
  const navigate = useNavigate();
  const setUserToken = useSetRecoilState(Token);
  const setMyAccountName = useSetRecoilState(MyAccountName);
  const setIsLoginState = useSetRecoilState(IsLoginState);

  //  6  
  const validatePassword = password => {
    if (password.length < 6) {
      return false;
    }
    return true;
  };

  // input    
  const isValidForm = email.length > 0 && password.length > 0;

  const LoginFunc = async e => {
    e.preventDefault();

    //   
    const validResult = await IsValidEmailAPI(email);

    //   
    if (!password || !validatePassword(password)) {
      setFailedMessage('* 6  .');
      return;
    }

    //       
    try {
      const data = await LoginAPI({
        user: {
          email,
          password,
        },
      });

      //     
      const loginSuccess = () => {
        setIsLoginState(1);
        navigate('/mainpage');
      };

      if (data.status === 422) {
        setFailedMessage('* /  .');
      } else if (data.user) {
        console.log(' !');
        setUserToken(data.user.token);
        setMyAccountName(data.user.accountname);
        loginSuccess();
      } else {
        console.log(' ');
      }
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
      <Sform onSubmit={LoginFunc}>
        <SInputBox title="" id="email" type="text" value={email} onChange={e => setEmail(e.target.value)} />
        <SInputBox
          title=""
          id="password"
          type="password"
          value={password}
          onChange={e => setPassword(e.target.value)}
          FailedMessage={FailedMessage}
        />
        <Button size="Large" disabled={!isValidForm}>
          
        </Button>
      </Sform>
    </>
  );
}

export default Login;

const Sform = styled.form`
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-top: 20px;

  button {
    margin-top: 2px;
    margin-bottom: 15px;
  }
`;
