import React from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';

const Button = ({ size, children, active, disabled, color, navigate, stateValue }) => {
  const navi = useNavigate();

  const handleClick = () => {
    if (navigate) {
      navigate === '-1' ? navi(-1) : navi(navigate, { state: stateValue });
    }
  };

  return (
    <SButton type="submit" size={size} active={active} disabled={disabled} color={color} onClick={handleClick}>
      {children}
    </SButton>
  );
};

export default Button;

const SizeValue = size => {
  const sizes = {
    Large: {
      width: '322px',
      height: '44px',
      fontSize: '16px',
      fontWeight: 'normal',
    },
    Medium: {
      width: '120px',
      height: '34px',
      fontSize: '14px',
      fontWeight: 'normal',
    },
    MediumS: {
      width: '90px',
      height: '32px',
      fontSize: '14px',
      fontWeight: 'normal',
    },
  };

  return sizes[size] || sizes.MediumS;
};

const SButton = styled.button`
  //      ( )
  ${({ size }) => {
    const { width, height, fontSize, fontWeight } = SizeValue(size);
    return `
      width: ${width};
      height: ${height};
      font-size: ${fontSize};
      font-weight: ${fontWeight};
    `;
  }}
  //   main color, gray  , white     
  background: ${({ color }) => {
    if (color === 'gray') {
      return 'var(--deepgray)';
    } else if (color === 'white') {
      return '#FFFFFF';
    } else {
      return '#961F1F';
    }
  }};
  //    
  // ,    
  color: ${({ active }) => {
    if (active) {
      return '#767676';
    } else {
      return '#FFFFFF';
    }
  }};

  border: ${({ active }) => {
    if (active) {
      return '1px solid #DBDBDB';
    } else {
      return 'none';
    }
  }};

  border-radius: 44px;
  cursor: pointer;

  // disabled    (     )
  &:disabled {
    background-color: #dcb6b6;
    cursor: initial;
  }
`;
